@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700,900);
@import url(https://cdn.jsdelivr.net/npm/hack-font/build/web/hack.css);
html,
body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  overflow-x: hidden; }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

*,
*::before,
*::after {
  box-sizing: border-box; }

*::-webkit-scrollbar {
  width: 4px;
  height: 4px; }

*::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background-color: get-color(main-grey, light); }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-size: 1em;
  font-weight: 400; }

.header path {
  fill: #000000;
  -webkit-transition: fill 150ms;
  transition: fill 150ms; }

.header path:hover {
  fill: #f1e05a; }

.app__wrapper {
  padding: 20px 0;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center; }

.fetchPeople__input {
  padding: 8px 12px;
  border: solid 2px;
  outline: none;
  width: 60%;
  font-size: 16px;
  font-weight: 700;
  background: #fff;
  -webkit-transition: border 75ms linear;
  transition: border 75ms linear;
  border-radius: 3px; }
  .fetchPeople__input:focus {
    border: solid 2px #f1e05a; }

.fetchPeople__list {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  margin-top: 28px; }

.fetchPeople__card {
  font-weight: 500;
  font-family: 'hack';
  font-size: 16px;
  padding: 8px 12px;
  border: solid 2px;
  margin-right: 24px;
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 58px;
  justify-content: center; }
  .fetchPeople__card--from {
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 300; }
  .fetchPeople__card:last-child {
    margin-bottom: 0; }

